body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{

}
.AppHeader{

}
.SearchPanel{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
.ItemStatusFilter{
    text-align: right;
}
.TodoList{

}
.TodoListItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 25px;
    cursor: pointer;
    color: #000000;
}

.TodoListItem button {
    margin: 0 7px;
}

.done {
    text-decoration: line-through;
}

.important {
    color: #dd2c00;
    font-weight: 500;
}


.ItemAddForm {
    border: 1px solid #e0e0e0;
    padding: 0 15px;
}

.ItemAddForm form {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

inp{
    width: 20px;
}




